@charset "UTF-8";
body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: #141414;
}

ul, ol {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: top;
  border: none;
}

body {
  font-family: "Roboto", "Noto Sans KR", sans-serif;
  color: #141414;
  overflow-x: hidden;
}

body, a {
  cursor: none;
}

body.mobile, body.mobile a {
  cursor: auto;
}

.mouse_cursor {
  position: absolute;
  z-index: 1000;
  width: 20px;
  height: 20px;
  background-color: #0BB7B1;
  border-radius: 50%;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  pointer-events: none;
  mix-blend-mode: exclusion;
}
.mouse_cursor.active {
  width: 500px;
  height: 354px;
  transform: translate(-50%, -50%);
  background: url(./images/cursor_arrow.svg) no-repeat center;
  background-size: contain;
}
@media only screen and (max-width: 760px) {
  .mouse_cursor.active {
    width: 330px;
    height: 240px;
  }
}
.mobile .mouse_cursor {
  display: none;
}

header {
  display: flex;
  position: fixed;
  width: 100vw;
  padding: 20px 20px 0px 20px;
  box-sizing: border-box;
  justify-content: space-between;
  color: #fff;
  mix-blend-mode: exclusion;
  font-style: italic;
  z-index: 1000;
  font-weight: 300;
}
header a {
  height: -moz-fit-content;
  height: fit-content;
}
header a h1 {
  font-size: 18px;
  text-decoration-line: underline;
  font-weight: 300;
  letter-spacing: -1px;
  color: #fff;
}
header nav ul li {
  text-align: right;
}
header nav ul li a {
  color: #fff;
}
header nav ul li a.active, header nav ul li a:hover {
  text-decoration-line: underline;
}

footer {
  overflow-x: hidden;
  font-size: 1em;
  background-color: #141414;
  height: auto;
  color: #F0F0F0;
}
footer .inner {
  margin: 0 auto;
  max-width: 1240px;
  height: 100%;
  box-sizing: border-box;
  color: #F0F0F0;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 100px;
}
footer .inner a {
  color: #F0F0F0;
}
footer .inner .top {
  margin-top: 10vh;
}
footer .inner .top .title {
  transform: translateY(-50px);
  opacity: 0;
  transition: transform 1.5s, opacity 1.5s;
}
footer .inner .top .title h2 {
  position: relative;
  margin: 0 auto;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 9.375vw;
  font-family: "Libre Caslon Display";
  font-weight: 400;
}
footer .inner .top .title h2::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0.5208vw;
  transform: translateX(-50%);
  width: 110%;
  height: 2px;
  background-color: #F0F0F0;
}
footer .inner .top .title h2::after {
  content: "✺";
  position: absolute;
  font-size: 4.1667vw;
  font-weight: 400;
  right: -4.1667vw;
  top: -1.5625vw;
}
@media only screen and (max-width: 760px) {
  footer .inner .top .title h2 {
    font-size: 4.453125em;
  }
  footer .inner .top .title h2::before {
    bottom: 0.24738rem;
  }
  footer .inner .top .title h2::after {
    font-size: 1.97918125rem;
    right: -1.97918125rem;
    top: -0.7421875rem;
  }
}
footer .inner .top .link {
  transform: translateY(-50px);
  opacity: 0;
  transition: transform 1.5s, opacity 1.5s;
  transition-delay: 0.5s;
  margin-top: 60px;
}
footer .inner .top .link ul {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
footer .inner .top .link ul li {
  position: relative;
}
footer .inner .top .link ul li a {
  font-size: 1.5625em;
  font-style: italic;
  font-weight: 200;
}
footer .inner .top .link ul li::after {
  content: "";
  position: absolute;
  width: 5px;
  height: 5px;
  left: 50%;
  bottom: -15px;
  transform: translateX(-50%);
  background-color: #F0F0F0;
  border-radius: 100%;
}
footer .inner .top .link ul li:last-child::after {
  display: none;
}
footer .inner .top .link .github {
  display: block;
  margin-top: 30px;
}
footer .inner .bottom {
  transform: translateX(50px);
  opacity: 0;
  transition: transform 1.5s, opacity 1.5s;
  transition-delay: 1s;
  text-align: right;
  margin-bottom: 2vh;
  font-size: 14px;
  font-weight: 300;
  word-break: keep-all;
}
@media only screen and (max-width: 1240px) {
  footer .inner {
    padding: 0 20px;
  }
}
@media only screen and (max-width: 470px) {
  footer {
    font-size: 0.75em;
  }
}
@media only screen and (max-width: 370px) {
  footer {
    font-size: 0.6em;
  }
}
footer.active .inner .top .title, footer.active .inner .top .link {
  transform: translateY(0px);
  opacity: 1;
}
footer.active .inner .bottom {
  transform: translateX(0px);
  opacity: 1;
}

section h2 {
  font-size: 4em;
}

